@import url(./variables.css);




.-intro h1 {
    text-align: left;
    font-size: 40px;
    margin-left: 2rem;
    margin-bottom: 0.3rem;
    

}



.-follow{
    font-size: 50px;
    margin: auto;
}

.intro-container{
    margin-top: 300px;
    z-index: 10;
    position: fixed;
    
}

.intro-bar {
    background-color: var(--ac-gray);
    padding: 1rem;
    display: block;
    padding: 1rem;
    padding-left: 2rem;
    padding-top: 0.3rem;
    /* margin-top: 300px; */
    max-width: 500px;
    -webkit-filter: drop-shadow(8px 8px 5px var(--ac-blue-dark));
            filter: drop-shadow(8px 8px 5px var(--ac-blue-dark));
    
}

.intro-content{
    text-justify: distribute;
    text-align: left;
}

.intro-content h3 {
    margin-bottom: 0.2rem;
}

.intro-content p {
    color: #aaa;
}

@-webkit-keyframes glow {
    0% {
        opacity: 1;
    }
    25% {
        -webkit-filter: drop-shadow(0px 0px 10px var(--ac-white));
                filter: drop-shadow(0px 0px 10px var(--ac-white));
        
    }
    50% {
        opacity: 1;
        fill: var(--ac-white);
    }
    75% {
        opacity: 1;
    }
    100%{
        opacity: 1;
    }

}

@keyframes glow {
    0% {
        opacity: 1;
    }
    25% {
        -webkit-filter: drop-shadow(0px 0px 10px var(--ac-white));
                filter: drop-shadow(0px 0px 10px var(--ac-white));
        
    }
    50% {
        opacity: 1;
        fill: var(--ac-white);
    }
    75% {
        opacity: 1;
    }
    100%{
        opacity: 1;
    }

}


.social-icons {
    width: 80px;
    -webkit-animation: glow 5000ms ease-in infinite;
            animation: glow 5000ms ease-in infinite;
    fill-opacity: 0; 
    stroke-width: 2; 
    stroke:var(--ac-white);
}
.facebook {
    fill:var(--ac-white);
    stroke: var(--ac-red);
    
}
.linkedin {
    fill:var(--ac-white);
    stroke: var(--ac-red);
    

}
.instagram {
    fill:var(--ac-white);
    fill-opacity: 1;
}
.youtube {
    fill:var(--ac-red);
    stroke: var(--ac-red);
    
}
.googleplus {
    fill:var(--ac-white);
}
.github {
    fill:var(--ac-red);
}

.social-icons:hover {
    -webkit-transition: fill-opacity 2000ms, -webkit-filter 1500ms;
    transition: fill-opacity 2000ms, -webkit-filter 1500ms;
    -o-transition: fill-opacity 2000ms, filter 1500ms;
    transition: fill-opacity 2000ms, filter 1500ms;
    transition: fill-opacity 2000ms, filter 1500ms, -webkit-filter 1500ms;
    -webkit-animation: none;
            animation: none;
    -webkit-filter: drop-shadow(0px 0px 10px var(--rgb-red));
            filter: drop-shadow(0px 0px 10px var(--rgb-red));
    fill-opacity: 1;
    fill: var(--ac-red);
}

.youtube:hover {
    -webkit-transition: fill-opacity 2000ms, -webkit-filter 1500ms;
    transition: fill-opacity 2000ms, -webkit-filter 1500ms;
    -o-transition: fill-opacity 2000ms, filter 1500ms;
    transition: fill-opacity 2000ms, filter 1500ms;
    transition: fill-opacity 2000ms, filter 1500ms, -webkit-filter 1500ms;
    -webkit-animation: none;
            animation: none;
    -webkit-filter: drop-shadow(0px 0px 10px var(--ac-white));
            filter: drop-shadow(0px 0px 10px var(--ac-white));
    fill-opacity: 1;
    fill: var(--ac-red);
}


