@import url(./variables.css);


.border-box {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.page-title{
    float: left;
    vertical-align: middle;
    font-size: 1.1em;
}
.br-100 {
    border-radius: 100%;
}


.nav-btn{
    width: 40px;
    margin: auto;


}

.dib {
    display: inline-block;
}

.dt {
    display: table;
}

.dtc {
    display: table-cell;
}

.h2 {
    height: 2rem;
}

.link {
    text-decoration: none;
    -webkit-transition: color .15s ease-in;
    -o-transition: color .15s ease-in;
    transition: color .15s ease-in;
}

.link:link, .link:visited {
    -webkit-transition: color .15s ease-in;
    -o-transition: color .15s ease-in;
    transition: color .15s ease-in;
}

.link:hover {
    -webkit-transition: color .15s ease-in;
    -o-transition: color .15s ease-in;
    transition: color .15s ease-in;
}

.link:active {
    -webkit-transition: color .15s ease-in;
    -o-transition: color .15s ease-in;
    transition: color .15s ease-in;
}

.link:focus {
    -webkit-transition: color .15s ease-in;
    -o-transition: color .15s ease-in;
    transition: color .15s ease-in;
    outline: 1px dotted currentColor;
}

.w2 {
    width: 3rem;
}

.w-25 {
    width: 25%;
}

.w-75 {
    width: 75%;
}

.w-90 {
    width: 90%;
}

.w-100 {
    width: 100%;
}

.dark-gray {
    color: #4d4d4f;
}

.mid-gray {
    color: #555;
}

.pa3 {
    padding: 0.5rem;
}

.mr3 {
    margin-right: 1rem;
}

.tr {
    text-align: right;
}

.f6 {
    font-size: .875rem;
}

.v-mid {
    vertical-align: middle;
}

.dim {
    opacity: 1;
    -webkit-transition: opacity .15s ease-in;
    -o-transition: opacity .15s ease-in;
    transition: opacity .15s ease-in;
}

.dim:hover, .dim:focus {
    opacity: .5;
    -webkit-transition: opacity .15s ease-in;
    -o-transition: opacity .15s ease-in;
    transition: opacity .15s ease-in;
}

.dim:active {
    opacity: .8;
    -webkit-transition: opacity .15s ease-out;
    -o-transition: opacity .15s ease-out;
    transition: opacity .15s ease-out;
}

#joinus{
    color : var(--ac-red);
}

.nav{
    padding-left: 10px;
}
@media screen and (min-width: 30em) {
    .ph5-ns {
        padding-left: 1rem;
        padding-right: 4rem;
    }

    .mr4-ns {
        margin-right: 2rem;
    }

    .f5-ns {
        font-size: 1rem;
    }
}


