
@import url(./variables.css);

@-webkit-keyframes glow-bkg {
    0% {
        -webkit-filter: drop-shadow(8px 8px 5px var(--ac-blue-dark));
                filter: drop-shadow(8px 8px 5px var(--ac-blue-dark));
    }
    50% {
        -webkit-filter: drop-shadow(8px 8px 8px var(--ac-blue));
                filter: drop-shadow(8px 8px 8px var(--ac-blue));
        
    }
    100% {
        -webkit-filter: drop-shadow(8px 8px 5px var(--ac-blue-dark));
                filter: drop-shadow(8px 8px 5px var(--ac-blue-dark));
    }
}

@keyframes glow-bkg {
    0% {
        -webkit-filter: drop-shadow(8px 8px 5px var(--ac-blue-dark));
                filter: drop-shadow(8px 8px 5px var(--ac-blue-dark));
    }
    50% {
        -webkit-filter: drop-shadow(8px 8px 8px var(--ac-blue));
                filter: drop-shadow(8px 8px 8px var(--ac-blue));
        
    }
    100% {
        -webkit-filter: drop-shadow(8px 8px 5px var(--ac-blue-dark));
                filter: drop-shadow(8px 8px 5px var(--ac-blue-dark));
    }
}


.BackgroundLogo {
    -webkit-animation: glow-bkg 6000ms ease-in-out infinite;
            animation: glow-bkg 6000ms ease-in-out infinite;
    width: 30%;
    margin: 2%;
}


